}

.header-top__language-swith {
  display: flex;
  height: 24px;
}

.header-top__language-swith a {
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 1px;
  color: #FFFFFF;
  text-transform: uppercase;
  padding: 5px 7px;
  display: inline-block;
}

.language-swith__link:not(:last-of-type):hover {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
}

.header-top__adres,
.face-boock {
  display: none;
}

.header-top {
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
  margin-top: 9px;
  margin-bottom: 10px;
  position: relative;
}

.phone {
  align-self: center;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #FFFFFF;
}

.header-home__background {
  background-image: url(../img/svg/Homepage/Assets/symbolh.svg), linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
  background-repeat: no-repeat, repeat;
  background-position: center top, center center;
  background-size: 524px 600px, cover;
  background-color: #323264;
  border-radius: 0 0 12px 12px;
  position: relative;
}
.header-home__background::after {
  content: "";
  background-color: #CACADB;
  height: 10px;
  position: absolute;
  bottom: -10px;
  left: 20px;
  right: 20px;
  border-radius: 0 0 12px 12px;
}
.header-home__background::before {
  content: "";
  background-color: #E7E7F2;
  height: 20px;
  position: absolute;
  bottom: -20px;
  left: 40px;
  right: 40px;
  border-radius: 0 0 12px 12px;
}

.header-home {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-rows: min-content min-content;
}

.header-top-background {
  grid-column: span 2;
  background: rgba(245, 245, 250, 0.2);
  margin: 0 -15px;
}

.header-menu {
  display: flex;
  align-items: center;
}

.header-menu__not-mobile {
  background: white;
  padding: 12px;
  position: absolute;
  right: 0;
  width: 100%;
  top: 0px;
  z-index: 1;
}

.header-menu__not-mobile li:not(:last-child) {
  margin-bottom: 15px;
}

.header-menu__not-mobile a {
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #24A3FF;
  border-radius: 12px;
  display: block;
  padding: 7px 15px;
  background-color: #F5F5FA;
}

.not-mobile_none {
  right: 100%;
}

.logo-adaptive {
